.Cart{
    width: 55vw;
    margin: 0 auto;
    padding: 15px;
    box-sizing: border-box;
    font-size: 13px; 
    margin-bottom: 100px;

}
.Cart .PurchasedImg{
    width: 15vw;
    margin: auto;

}
.PurchasedInfo{
    padding: 10px 0;
     background-color: rgba(238, 238, 238, 0.719);
     width: 500px;
    position: relative;
    margin: 0 0 0 auto;
     
}

.ProductPurchased{
    display: flex;
    margin-bottom: 10px;
}

.CartFooter{
    border-top: 2px solid #333;
    position: fixed;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 60%;
    border-radius: 6px;
}

.CartFooter .PriceLine{
    padding: 15px;
    background-color: rgba(172, 190, 177, 0.596);
    font-size: 19px;
    font-weight: 400;
    text-align: right;
}

.CartFooter .PriceLine .TotalPrice{
    font-weight: 700;
}
.PurchaseOrderBtn{
    width: 200px;
    font-size: 18px;
    margin: 60px 0 0 auto;
}