body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Nav-Element {
  width: 9vw;
  padding: 14px;
  text-decoration: none;
  color: rgb(0, 0, 0);
  border-radius: 3px;
  box-sizing: border-box;
  position: relative;
}

.Nav-Element:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.NumberOfPurchases{
  color:rgb(185, 9, 9);
  font-size: .9em;
  position: absolute;
  bottom: 4px;
}

.Nav-Element.active {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  color: rgb(37, 37, 37);
  font-weight: bold;
}

.Nav {
  display: flex;
  justify-content: flex-end;
  flex-flow: row wrap;
  box-sizing: border-box;
  background-color: rgba(255, 255, 255, 0);
  text-transform: uppercase;
  font-weight: 700;
  font-size: .9em;
  background-color: #fff;
  border-bottom: 1px solid rgba(143, 143, 143, 0.582);
  margin: 0 20px 10px;
}

.Input,
.Textarea {
  margin: 10px;
  padding: 8px;
  font-size: 1.1em;
  width: 90%;
  margin: 10px auto;
  font-weight: 600;
  display: block;
  box-sizing: border-box;
}

.Input {
  text-align: center;
}

.Input:focus,
.Textarea:focus, .Select:focus {
  border: 1px solid #555;
  outline-width:0;
}
.Label {
  font-weight: 600;
  text-transform: uppercase;
  font-size: 1.1em;
}

.Textarea {
  resize: none;
  height: 15vh;
}

.Text {
  font-size: 1.2em;
  color: #555;
}

.Select{
  padding: 3px;
}
.ImageContainer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 30vw;
}

.FrontImage {
  width: 20vw;
  /* height: 30vh; */
  display: flex;
  flex-basis: 80%;
  margin: 20px auto;
}

.EachImageContainer {
  display: flex;
  flex-direction: column;
  width: 9vw;
  /* height: 8vh; */
  margin: 12px 1%;
  position: relative;
}

.AllImages {
  width: 10vw;
}

.AllImages:hover {
  opacity: 0.2;
  cursor: pointer;
}

.EachImageContainer:hover.EachImageContainer::after {
  content: "Select";
  pointer-events: fill;
  cursor: pointer;
  font-size: 1.5em;
  font-weight: bold;
  color: rgb(58, 58, 58);
  position: absolute;
  left: 55%;
  top: 30%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  opacity: 1;
  pointer-events: none;
}
.EachImageContainer:hover .AllImages {
  opacity: 0.2;
}

.RemoveItem {
  cursor: pointer;
  font-size: 1.2em;
  font-weight: bold;
  position: absolute;
  right: 0;
  top: -30%;
  color: rgba(255, 0, 0, 0.836);
  opacity: 1;
}

.NoImg {
  height: 30vh;
  width: 20vw;
  box-shadow: inset 0px 0px 20px 0px rgba(97, 97, 97, 1);
  display: flex;
  margin: 20px auto;
  border: 1px solid #999;
}

.NoImg p {
  margin: auto;
  font-weight: 600;
  font-size: 1.3em;
}

.Button {
  box-sizing: border-box;
  padding: 8px;
  margin-bottom: 10px;
  border-radius: 3px;
  background-color: rgb(206, 206, 206);
  border: none;
  font-weight: 600;
  font-size: 1em;
  color: aliceblue;
  width: 100%;
}

.Button.Confirm{
  background-color: rgba(25, 187, 66, 0.933);
}

.Button.Success {
  background-color: rgb(67, 148, 42);
}

.Button.Change{
  background-color: rgba(255, 0, 0, 0.589);
  font-weight: 600;
  font-size: 1em;
}
.Button.Danger {
  background-color: rgb(255, 0, 0);
  
}

.Button.Order {
  background-color: rgb(255, 230, 86);
  color: rgb(0, 0, 0);

}
.Button.Button.Order:hover {
  background-color: rgb(231, 197, 0);
}
.Button:hover{
 -webkit-transform: scale(1.1);
         transform: scale(1.1);
 -webkit-transition: 0.2s ease;
 transition: 0.2s ease
}
.Button:hover {
  cursor: pointer;
}

.Button:focus {
  outline: none;
}

.Remove{
  background-color: rgb(255, 0, 0);
}


.ItemInfo {
  display: flex;
  flex-direction: column;
  flex-wrap: inherit;
  width: 50vw;
  max-height: 79vh;
  overflow: auto;
  padding: 20px;
}

.ItemInfo .Specifications {
  text-transform: uppercase;
  margin: 3vh;
  font-weight: 600;
  font-size: 1.4em;
  letter-spacing: 0.6vw;
}

.ViewItem {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 5vh 5vw;
}

.ItemInfo p {
  text-transform: uppercase;
}

.AddNewItem {
}

.uploadPictureContainer {
  width: 10vw;
}

/* Home View */

.Short-Product-View {
  display: flex;
  flex-direction: column;
  width: 17vw;
  box-sizing: border-box;
  padding: 1vh 0;
  font-size: .9em;
}

.NotFound{
  font-size: 24px;
  margin-top: 40px;
}

.OrderBtn{
  width: 140px;
  box-sizing: border-box;
  margin: 10px auto;
}

.Short-Product-View .ShortProductView-Image {
  margin: auto;
  max-width: 14vw;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: 0.4s ease-in-out;
  transition: 0.4s ease-in-out;
}

.Short-Product-View .ShortProductView-Image:hover {
  cursor: pointer;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.Short-Product-View .Title {
  width: 15vw;
  margin: 3vh auto;
}

.Short-Product-View .Title:hover {
  cursor: pointer;
}

.Short-Product-View p {
  color: rgb(116, 116, 116);
  margin-bottom: 1vh;
  font-weight: 400;
  font-size: 1em;
}
.Short-Product-View p b {
  font-weight: 700;
  color: rgb(97, 96, 96);
  font-size: 1.1em;
}

.Short-Product-View .Info-Container, .PurchaseInfo {
  width: 80%;
  margin: auto;
  margin-top: 3vh;
}
.Short-Product-View .Price, .ShortPurchasesView .Price {
  margin: 2vh;
  color: rgb(255, 53, 53);
  font-size: 1.2em;
  font-weight: 700;
  letter-spacing: 3px;
}

/* Purchase */
.ShortPurchasesView{
  display: flex;
  width: 100%;
  box-sizing: border-box;
  padding-bottom: 10px ;
  font-size:1.2em;
  position: relative;
  height: 28vh;
}
.ShortPurchasesView + .ShortPurchasesView{
  border-top: 1px solid #333;
}

.ShortPurchasesView .Title{
  position: absolute;
  left: 50%;
  top: 0;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.ShortPurchaseImage{
  margin: auto;
  width: 16vw;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: 0.4s ease-in-out;
  transition: 0.4s ease-in-out;
  overflow: hidden;
}

.ShortPurchasesView h2:hover{
  cursor: pointer;
}

.ShortPurchaseImage:hover{
  cursor: pointer;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.PurchaseInfo{
  align-self: center;
  padding: 8px;
  border-radius: 3px;
  margin: 0;
  margin-left: 30px;
  width: 35vw;
  background-color: rgba(190, 190, 107, 0.349);
}

.RemoveBtn{
  width: 30px;
  margin-top: 5px;
}

.PriceContainer{
  align-self: flex-end;
  text-align: center;
  width: 50%;
}

.PriceContainer .Price{
  letter-spacing: normal;
  margin: 0 0 15px;
}

.PriceContainer select{
  margin-right: 20px;
  width: 40px;
}

/* Order Summary */

.ProductSummary-Container{
  width: 100%;
  display: flex;
  padding: 10px 0;
  height: 20vh;
  flex-wrap: wrap;
  position: relative;
  justify-content: space-around;
  overflow: hidden;
}

.ProductSummary-Container + .ProductSummary-Container{
  margin-top: 15px;
  border-top: 1px solid rgb(175, 167, 48);
}

.ProductSummary-Container .Title {
  position: absolute;
  font-size: 1.1em;
  left: 52%;
  top:0;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.ProductSummary-Container .ProductSummary-Image{
  width: 11vw;
  margin: auto 0;
}

.ProductSummary-Container .ProductSummary-Info{
  width: 20vw;
  align-self: center;
}

.ProductSummary-Container .ProductSummary-Price{
  align-self: center;
}

.ShortProductView-Container {
  display: flex;
  flex-wrap: wrap;
  width: 70vw;
  justify-content: flex-start;
}

.OptionsContainer {
  border-bottom: 1px solid rgba(192, 192, 192, 0.548);
  box-sizing: border-box;
  padding-top: 2vh;
}

.OptionsContainer-Item {
  padding: 5px;
  cursor: pointer;
  list-style: none;
  font-size: 1.2em;
  margin-bottom: 5px;
  font-weight: 600;
}

.OptionsContainer-Title {
  font-size: 1.4em;
  margin-bottom: 10px;
}

.OptionsContainer-Item:hover {
  background-color: #94adff6b;
  background: -webkit-linear-gradient(
    top,
    #3366ffb6 0%,
    #6b90ff 47%,
    #ccd8ff 100%
  );
  background: linear-gradient(
    to bottom,
    #3366ffb6 0%,
    #6b90ff 47%,
    #ccd8ff 100%
  );
  color: #fff;
}
.isActive {
  background-color: rgba(0, 17, 255, 0.329);
}

.Filter {
  display: flex;
  flex-direction: column;
  width: 20vw;
  height: 100vh;
  box-sizing: border-box;
  margin-left: 40px;
  padding-top: 3vh;
  border: 1px solid rgba(66, 66, 66, 0.575);
}

.ViewItems {
  display: flex;
  flex-direction: row;
  background-color: #fff;
}

.ProductHeader-ImageContainer {
  width: 40vw;
}

.ProductHeader-ImageContainer .ProductHeader-Images {
  display: flex;
  flex-direction: row;
  width: 75%;
  box-sizing: border-box;
  margin: auto;
  box-shadow: inset 0px 0px 8px 6px rgba(201, 201, 201, 0.49);
  padding: 5px;
  border-radius: 9px;
}

.ProductHeader-ImageContainer .ProductHeader-FirstImg {
  width: 40vw;
  height: 40vh;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  margin: 10px auto;
}

.ProductHeader-ImageContainer .ProductHeader-FirstImg img {
  box-sizing: border-box;
  margin: auto;
  max-height: 100%;
  max-width: 100%;
}

.ProductHeader-ImageContainer
  .ProductHeader-Images
  .ProductHeader-Images-Image {
  box-sizing: border-box;
  padding: 1px;
  flex-direction: row;
  margin: auto;
  width: 5vw;
  display: flex;
}

.RightArrow {
  width: 0;
  height: 0;
  border: 0 solid transparent;
  border-bottom-width: 18px;
  border-top-width: 18px;
  border-left: 22px solid rgba(153, 153, 153, 0.767);
  margin: auto 15px;
}

.LeftArrow {
  width: 0;
  height: 0;
  border: 0 solid transparent;
  border-bottom-width: 18px;
  border-top-width: 18px;
  border-right: 22px solid rgba(153, 153, 153, 0.767);
  margin: auto 15px;
}

.Arrow:hover {
  cursor: pointer;
}

.PropName {
  color: rgb(116, 116, 116);
  font-weight: 700;
}
.PropPrice {
  color: rgb(255, 0, 0);
  font-size: 1.4em;
  font-weight: 700;
  letter-spacing: 5px;
  line-height: 50px;
}
.ProductHeader-ShortInfo-Container {
  display: flex;
  flex-direction: column;
  width: 45%;
  margin: 0 auto;
  border-top: 2px solid rgb(110, 110, 110);
  border-bottom: 2px solid rgb(110, 110, 110);
  padding: 30px;
  box-sizing: border-box;
}
.ProductHeader-ShortInfo {
  margin: auto;
}

.ProductHeader-ShortInfo-Container .OrderBtn{
  width: 300px
}

.ProductHeader-ShortInfo-Container .RemovePurchase{
  width: 30px;
  font-size: .6em;
  position: absolute;
  right: 10px;
  top: 10px;
}
.ProductHeader-Container {
  width: 80vw;
  margin: auto;
  box-sizing: border-box;
}
.ProductHeader-ShortDetails {
  flex-direction: row;
  flex-wrap: wrap;
  display: flex;
  box-sizing: border-box;
  padding-top: 5vh;
}
.ProductHeader-Title {
  font-size: 2.4em;
  font-weight: 600;
}

.FullProductDetails {
  width: 90%;
  margin: 20px auto 0;
}

.FullProductDetails h2 {
  margin: 50px auto;
}

.FullProductDetails table {
  width: 100%;
  font-size: 1.1em;
}

.FullProductDetails table tr {
  height: 50px;
}

.FullProductDetails table tr:nth-of-type(odd) {
  background-color: rgba(115, 211, 255, 0.363);
}

.FullProductDetails table th {
  font-size: 1.2em;
  font-weight: 600;
  width: 50%;
}

.Product {
  width: 80vw;
  margin: 0 auto;
  background-color: #fff;
  padding: 40px;
}
.ProductHeader-OrderStatus {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  border-bottom: 1px solid rgb(145, 145, 145);
  width: 40vw;
  font-size: 0.9em;
  margin: 10px 0 30px auto;
  padding: 20px;
  box-sizing: border-box;
}
.OrderStatus-Item {
  width: 100px;
  margin: 0 10px;
  box-sizing: border-box;
}

.Auth-Form {
  width: 30vw;
  padding: 5vh 0;
  box-sizing: border-box;
  border-top: 1px solid #999;
  border-bottom: 1px solid #999;
}

.Auth-Form h2 {
  margin-bottom: 20px;
}

.Auth {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60vh;
}

.HelpMessage{
  display: flex;
  justify-content: center;
  color: rgb(228, 7, 7);
  font-weight: 700;
  font-size: 1.3em;
}

.OrdersContainer{
    width: 60vw;
    margin: 0 auto;
    font-size: 1em;
}

.OrdersContainer .OrdersContainer-Orders{
    flex-wrap: wrap;
    justify-content: center;
    display: flex;
}

.OrdersContainer .OrdersContainer-Order:hover{
    cursor: pointer;
    background-color: rgba(230, 221, 210, 0.692);
}

.OrdersContainer .OrdersContainer-Order{
    width: 200px;
    border: 3px solid rgb(173, 192, 68);
    padding: 8px;
    box-sizing: border-box;
    margin: 10px;
    border-radius: 5px;
}

.OrdersContainer-Order li{
    list-style: none;
}

.OrdersContainer .OrdersContainer-ShowOrder{
    margin: 20px 0 60px;
}
.AccountInfo{
    margin-top: 50px;
    width: 300px;
    margin: auto;
}
.AccountInfo h3{
    font-size: 1.4em;
}

.AccountInfo .EmailInfo{
    margin: 20px;
    font-weight: 600;
    font-size: 1.2em;
}
.AccountInfo .EmailInfo span{
    text-decoration: underline;
}

.ChangePasswordField{
    width: 290px;
    margin: 0 auto;
    -webkit-animation: create-element .5s ease;
            animation: create-element .5s ease;
}

.HelpPasswordMessage{
    background-color: rgba(218, 218, 218, 0.863);
    padding: 10px;
    margin:5px 0;
    box-sizing: border-box;
    border-radius: 3px;
    -webkit-animation: create-element 0.5s ease;
            animation: create-element 0.5s ease;
    font-weight: 600;
}
    
.HelpPasswordMessage.Error{
   color: rgba(212, 17, 17, 0.863);
}

.HelpPasswordMessage.Success{
   color: rgb(32, 141, 5);
}

.DeleteAccount{
    display: flex;
    justify-content: center;
     align-items: center;
}

.DeleteAccount .Box{
    width: 300px;
    border: 1px solid #333;
    padding: 30px;
    margin: 50px 0;
    background-color: rgba(204, 204, 204, 0.479);
    border-radius: 3px;
    font-size: 1.1em;    
-webkit-animation: create-element .5s ease;    
        animation: create-element .5s ease;
}

@-webkit-keyframes create-element{
    0%{
        opacity: 0;
        -webkit-transform: scale(0);
                transform: scale(0);
    }
    100%{
        opacity: 1;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes create-element{
    0%{
        opacity: 0;
        -webkit-transform: scale(0);
                transform: scale(0);
    }
    100%{
        opacity: 1;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
.Cart{
